import { create } from "zustand";
import type { PushMessage } from "@/gen/gateway/v1/packet_pb";
import { UpdateReadPositionRequest } from "@/gen/gateway/v1/api_pb";
import { sessionClient } from "@/api/client";
import { SESSION_TYPES } from "@/constants";

/**
 * 
 *  protobuf resonance.gateway.v1.SessionInfo
 */
export interface SessionInfo {
  sessionId: string;
  name: string;
  type: keyof typeof SESSION_TYPES | number;
  avatarUrl?: string;
  unreadCount: number;
  lastReadSeq: number; // 
  maxSeqId: number; //  ()
  lastMessage?: {
    msgId: bigint;
    seqId: bigint;
    content: string;
    type: string;
    timestamp: bigint;
  };
}

interface SessionState {
  // 
  sessions: SessionInfo[];
  currentSessionId: string | null;
  isLoading: boolean;
  error: string | null;

  // Selectors
  getCurrentSession: () => SessionInfo | null;
  getSessionById: (sessionId: string) => SessionInfo | undefined;
  getTotalUnreadCount: () => number;

  // Actions
  setSessions: (sessions: SessionInfo[]) => void;
  upsertSession: (session: SessionInfo) => void;
  updateSession: (sessionId: string, updates: Partial<SessionInfo>) => void;
  removeSession: (sessionId: string) => void;
  setCurrentSessionId: (sessionId: string | null) => void;
  // incrementUnread  updateLastMessage  markAsRead
  markAsRead: (sessionId: string, seqId: number) => Promise<void>;
  updateLastMessage: (sessionId: string, message: PushMessage) => void;
  setIsLoading: (loading: boolean) => void;
  setError: (error: string | null) => void;
  clearError: () => void;
  reset: () => void;
}

export const useSessionStore = create<SessionState>((set, get) => ({
  // 
  sessions: [],
  currentSessionId: null,
  isLoading: false,
  error: null,

  // Selectors
  getCurrentSession: () => {
    const { sessions, currentSessionId } = get();
    return sessions.find((s) => s.sessionId === currentSessionId) || null;
  },

  getSessionById: (sessionId) => {
    return get().sessions.find((s) => s.sessionId === sessionId);
  },

  getTotalUnreadCount: () => {
    return get().sessions.reduce((sum, s) => sum + s.unreadCount, 0);
  },

  // Actions
  setSessions: (sessions) =>
    set({
      sessions,
    }),

  upsertSession: (session) =>
    set((state) => {
      const exists = state.sessions.find((s) => s.sessionId === session.sessionId);
      if (exists) {
        // 
        return {
          sessions: state.sessions.map((s) =>
            s.sessionId === session.sessionId ? { ...s, ...session } : s,
          ),
        };
      }
      // 
      return {
        sessions: [session, ...state.sessions],
      };
    }),

  updateSession: (sessionId, updates) =>
    set((state) => {
      const sessions = state.sessions.map((s) =>
        s.sessionId === sessionId ? { ...s, ...updates } : s,
      );
      return { sessions };
    }),

  removeSession: (sessionId) =>
    set((state) => ({
      sessions: state.sessions.filter((s) => s.sessionId !== sessionId),
      currentSessionId: state.currentSessionId === sessionId ? null : state.currentSessionId,
    })),

  setCurrentSessionId: (sessionId) =>
    set({
      currentSessionId: sessionId,
    }),

  markAsRead: async (sessionId, seqId) => {
    const { sessions } = get();
    const session = sessions.find((s) => s.sessionId === sessionId);
    if (!session) return;

    //  seqId  lastReadSeq 
    if (seqId <= session.lastReadSeq) return;

    // 
    const newUnreadCount = Math.max(0, session.maxSeqId - seqId);
    set((state) => ({
      sessions: state.sessions.map((s) =>
        s.sessionId === sessionId ? { ...s, lastReadSeq: seqId, unreadCount: newUnreadCount } : s,
      ),
    }));

    //  API
    try {
      const req = new UpdateReadPositionRequest({
        sessionId,
        seqId: BigInt(seqId),
      });
      await sessionClient.updateReadPosition(req);
    } catch (err) {
      console.error("Failed to update read position:", err);
      // 
    }
  },

  updateLastMessage: (sessionId, message) =>
    set((state) => {
      const timestamp = message.timestamp;
      const msgId = message.msgId;
      const seqId = Number(message.seqId); //  number 

      // 
      const updatedSessions = state.sessions.map((s) => {
        if (s.sessionId === sessionId) {
          //  maxSeqId
          const newMaxSeqId = Math.max(s.maxSeqId || 0, seqId);

          //  maxSeqId
          //  ""  ""
          //  App.tsx  unread  "Recalculate" 

          return {
            ...s,
            maxSeqId: newMaxSeqId,
            lastMessage: {
              msgId,
              seqId: message.seqId,
              content: message.content,
              type: message.type,
              timestamp,
            },
          };
        }
        return s;
      });

      // 
      const targetSession = updatedSessions.find((s) => s.sessionId === sessionId);
      if (!targetSession) {
        return { sessions: updatedSessions };
      }

      const otherSessions = updatedSessions.filter((s) => s.sessionId !== sessionId);

      return {
        sessions: [targetSession, ...otherSessions],
      };
    }),

  setIsLoading: (isLoading) =>
    set({
      isLoading,
    }),

  setError: (error) =>
    set({
      error,
    }),

  clearError: () =>
    set({
      error: null,
    }),

  reset: () =>
    set({
      sessions: [],
      currentSessionId: null,
      isLoading: false,
      error: null,
    }),
}));
