import { createConnectTransport } from "@connectrpc/connect-web";
import { createPromiseClient } from "@connectrpc/connect";
import { AuthService, SessionService } from "@/gen/gateway/v1/api_connect";
import { useAuthStore } from "@/stores/auth";
import {
  CreateSessionRequest,
  SearchUserRequest,
  GetContactListRequest,
} from "@/gen/gateway/v1/api_pb";
import { defaultApiBaseUrl, runtimeApiBaseUrl } from "@/config/runtime";

const baseUrl = runtimeApiBaseUrl || import.meta.env.VITE_API_BASE_URL || defaultApiBaseUrl();

//  token  transport
export const transport = createConnectTransport({
  baseUrl,
  //  token
  interceptors: [
    (next) => async (req) => {
      const token = useAuthStore.getState().accessToken;
      if (token) {
        req.header.set("Authorization", token);
      }
      return await next(req);
    },
  ],
});

export const authClient = createPromiseClient(AuthService, transport);

// SessionService  token transport
export const sessionClient = createPromiseClient(SessionService, transport);

// ==============  API  ==============

/**
 * 
 */
export async function createSession(params: {
  members: string[];
  name?: string;
  type: 1 | 2; // 1-, 2-
}): Promise<string> {
  const req = new CreateSessionRequest({
    members: params.members,
    name: params.name ?? "",
    type: params.type,
  });
  const resp = await sessionClient.createSession(req);
  return resp.sessionId;
}

/**
 * 
 */
export async function searchUsers(query: string): Promise<
  Array<{
    username: string;
    nickname: string;
    avatarUrl: string;
  }>
> {
  const req = new SearchUserRequest({ query });
  const resp = await sessionClient.searchUser(req);
  return resp.users.map((u) => ({
    username: u.username,
    nickname: u.nickname,
    avatarUrl: u.avatarUrl,
  }));
}

/**
 * 
 */
export async function getContactList(): Promise<
  Array<{
    username: string;
    nickname: string;
    avatarUrl: string;
  }>
> {
  const req = new GetContactListRequest();
  const resp = await sessionClient.getContactList(req);
  return resp.contacts.map((c) => ({
    username: c.username,
    nickname: c.nickname,
    avatarUrl: c.avatarUrl,
  }));
}
