import { useState, useCallback, useEffect, useRef } from "react";
import { searchUsers, getContactList, createSession } from "@/api/client";
import { cn } from "@/lib/cn";

export interface ContactInfo {
  username: string;
  nickname: string;
  avatarUrl: string;
}

interface NewChatModalProps {
  isOpen: boolean;
  onClose: () => void;
  onSessionCreated: (sessionId: string) => void;
  currentUsername?: string;
}

type ChatMode = "single" | "group";

/**
 * 
 * Liquid Glass 
 * - 
 * -  + 
 */
export function NewChatModal({
  isOpen,
  onClose,
  onSessionCreated,
  currentUsername,
}: NewChatModalProps) {
  const [mode, setMode] = useState<ChatMode>("single");
  const [searchQuery, setSearchQuery] = useState("");
  const [searchResults, setSearchResults] = useState<ContactInfo[]>([]);
  const [contacts, setContacts] = useState<ContactInfo[]>([]);
  const [selectedUsers, setSelectedUsers] = useState<string[]>([]);
  const [groupName, setGroupName] = useState("");
  const [isSearching, setIsSearching] = useState(false);
  const [isLoadingContacts, setIsLoadingContacts] = useState(false);
  const [isCreating, setIsCreating] = useState(false);
  const [error, setError] = useState<string | null>(null);

  // 
  const handleSearch = useCallback(async () => {
    const query = searchQuery.trim();
    if (!query) {
      setSearchResults([]);
      return;
    }
    setIsSearching(true);
    setError(null);
    try {
      const result = await searchUsers(query);
      // 
      setSearchResults(result.filter((u) => u.username !== currentUsername));
    } catch (err) {
      console.error("[NewChatModal] Failed to search users:", err);
      setError("");
    } finally {
      setIsSearching(false);
    }
  }, [searchQuery, currentUsername]);

  // 
  useEffect(() => {
    if (mode === "single") {
      const timer = setTimeout(() => {
        handleSearch();
      }, 300);
      return () => clearTimeout(timer);
    }
  }, [mode, searchQuery, handleSearch]);

  // 
  const loadContacts = useCallback(async () => {
    setIsLoadingContacts(true);
    setError(null);
    try {
      const result = await getContactList();
      setContacts(result);
    } catch (err) {
      console.error("[NewChatModal] Failed to load contacts:", err);
      setError("");
    } finally {
      setIsLoadingContacts(false);
    }
  }, []);

  // 
  useEffect(() => {
    if (mode === "group" && contacts.length === 0) {
      loadContacts();
    }
  }, [mode, contacts.length, loadContacts]);

  // 
  useEffect(() => {
    setSelectedUsers([]);
    setGroupName("");
    setSearchQuery("");
    setSearchResults([]);
    setError(null);
  }, [mode]);

  // 
  const handleClose = useCallback(() => {
    setSelectedUsers([]);
    setGroupName("");
    setSearchQuery("");
    setSearchResults([]);
    setError(null);
    setMode("single");
    onClose();
  }, [onClose]);

  // 
  const toggleUser = useCallback(
    (username: string) => {
      if (mode === "single") {
        // 
        quickStartChat(username);
        return;
      }
      // 
      setSelectedUsers((prev) => {
        if (prev.includes(username)) {
          return prev.filter((u) => u !== username);
        }
        return [...prev, username];
      });
      setError(null);
    },
    [mode],
  );

  // 
  const quickStartChat = useCallback(
    async (username: string) => {
      setIsCreating(true);
      setError(null);
      try {
        const sessionId = await createSession({
          members: [username],
          type: 1,
        });
        onSessionCreated(sessionId);
        handleClose();
      } catch (err) {
        console.error("[NewChatModal] Failed to create session:", err);
        setError("");
      } finally {
        setIsCreating(false);
      }
    },
    [onSessionCreated, handleClose],
  );

  // 
  const handleCreateGroup = useCallback(async () => {
    if (selectedUsers.length === 0) {
      setError("");
      return;
    }
    if (selectedUsers.length < 2) {
      setError(" 2 ");
      return;
    }
    if (!groupName.trim()) {
      setError("");
      return;
    }

    setIsCreating(true);
    setError(null);
    try {
      const sessionId = await createSession({
        members: selectedUsers,
        name: groupName.trim(),
        type: 2,
      });
      onSessionCreated(sessionId);
      handleClose();
    } catch (err) {
      console.error("[NewChatModal] Failed to create group:", err);
      setError("");
    } finally {
      setIsCreating(false);
    }
  }, [selectedUsers, groupName, onSessionCreated, handleClose]);

  // Escape 
  const dialogRef = useRef<HTMLDivElement>(null);
  useEffect(() => {
    if (!isOpen) return;
    const handleKeyDown = (e: KeyboardEvent) => {
      if (e.key === "Escape" && !isCreating) {
        handleClose();
      }
    };
    document.addEventListener("keydown", handleKeyDown);
    return () => document.removeEventListener("keydown", handleKeyDown);
  }, [isOpen, isCreating, handleClose]);

  if (!isOpen) return null;

  const isGroupMode = mode === "group";
  const displayList = isGroupMode ? contacts : searchResults;
  const isLoading = isGroupMode ? isLoadingContacts : isSearching;

  return (
    <div
      className="lg-modal-overlay fixed inset-0 z-50 flex items-center justify-center px-4"
      role="dialog"
      aria-modal="true"
      aria-labelledby="new-chat-modal-title"
      onClick={() => !isCreating && handleClose()}
    >
      <div
        ref={dialogRef}
        className="lg-modal-content lg-glow-border lg-animate-in w-full max-w-md rounded-3xl"
        onClick={(e) => e.stopPropagation()}
      >
        {/*  */}
        <div className="flex items-center justify-between border-b border-white/40 p-4 dark:border-slate-200/10">
          <h2 id="new-chat-modal-title" className="text-lg font-semibold text-slate-900 dark:text-white"></h2>
          <button
            onClick={handleClose}
            className="rounded-full p-1 text-slate-500 transition-all duration-200 hover:bg-white/45 hover:text-slate-700 hover:shadow-sm dark:text-slate-300 dark:hover:bg-slate-700/55 dark:hover:text-slate-100"
            disabled={isCreating}
          >
            <svg className="h-5 w-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth={2}
                d="M6 18L18 6M6 6l12 12"
              />
            </svg>
          </button>
        </div>

        {/*  */}
        <div className="flex border-b border-white/40 dark:border-slate-200/10">
          <button
            onClick={() => setMode("single")}
            disabled={isCreating}
            className={cn(
              "flex-1 px-4 py-3 text-sm font-medium transition-all duration-200",
              mode === "single"
                ? "border-b-2 border-sky-500 text-sky-600 dark:text-sky-300"
                : "text-slate-500 hover:text-slate-700 hover:bg-white/30 dark:text-slate-400 dark:hover:text-slate-200 dark:hover:bg-slate-700/30",
              "disabled:opacity-50",
            )}
          >
            
          </button>
          <button
            onClick={() => setMode("group")}
            disabled={isCreating}
            className={cn(
              "flex-1 px-4 py-3 text-sm font-medium transition-all duration-200",
              mode === "group"
                ? "border-b-2 border-sky-500 text-sky-600 dark:text-sky-300"
                : "text-slate-500 hover:text-slate-700 hover:bg-white/30 dark:text-slate-400 dark:hover:text-slate-200 dark:hover:bg-slate-700/30",
              "disabled:opacity-50",
            )}
          >
            
          </button>
        </div>

        {/*  */}
        <div className="p-4">
          <div className="relative">
            <svg
              className="absolute left-3 top-1/2 h-4 w-4 -translate-y-1/2 text-gray-400"
              fill="none"
              stroke="currentColor"
              viewBox="0 0 24 24"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth={2}
                d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z"
              />
            </svg>
            <input
              type="text"
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
              placeholder={isGroupMode ? "" : ""}
              disabled={isCreating}
              className={cn(
                "lg-input w-full py-2 pl-10 pr-4 text-sm",
                "disabled:opacity-50",
              )}
              autoFocus
            />
          </div>
        </div>

        {/*  */}
        <div className="max-h-72 overflow-y-auto px-2">
          {isLoading ? (
            <div className="flex items-center justify-center p-8">
              <svg className="h-5 w-5 animate-spin text-gray-400" fill="none" viewBox="0 0 24 24">
                <circle
                  className="opacity-25"
                  cx="12"
                  cy="12"
                  r="10"
                  stroke="currentColor"
                  strokeWidth="4"
                />
                <path
                  className="opacity-75"
                  fill="currentColor"
                  d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"
                />
              </svg>
            </div>
          ) : displayList.length === 0 ? (
            <div className="flex flex-col items-center p-8 text-center">
              <svg
                className="mb-3 h-12 w-12 text-slate-400"
                fill="none"
                stroke="currentColor"
                viewBox="0 0 24 24"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={1.5}
                  d="M17 20h5v-2a3 3 0 00-5.356-1.857M17 20H7m10 0v-2c0-.656-.126-1.283-.356-1.857M7 20H2v-2a3 3 0 015.356-1.857M7 20v-2c0-.656.126-1.283.356-1.857m0 0a5.002 5.002 0 019.288 0M15 7a3 3 0 11-6 0 3 3 0 016 0zm6 3a2 2 0 11-4 0 2 2 0 014 0zM7 10a2 2 0 11-4 0 2 2 0 014 0z"
                />
              </svg>
              <p className="text-sm text-slate-500 dark:text-slate-400">
                {isGroupMode
                  ? contacts.length === 0
                    ? ""
                    : ""
                  : searchQuery
                    ? ""
                    : ""}
              </p>
            </div>
          ) : (
            <div className="space-y-1 p-2">
              {displayList.map((user) => {
                const isSelected = selectedUsers.includes(user.username);
                return (
                  <div
                    key={user.username}
                    onClick={() => !isCreating && toggleUser(user.username)}
                    className={cn(
                      "flex cursor-pointer items-center gap-3 rounded-xl border border-transparent p-3 transition-all duration-200",
                      "hover:border-white/40 hover:bg-white/45 hover:shadow-sm dark:hover:border-slate-200/10 dark:hover:bg-slate-700/55",
                      isCreating && "opacity-50 cursor-not-allowed",
                    )}
                  >
                    {/*  */}
                    {isGroupMode && (
                      <div
                        className={cn(
                          "flex h-5 w-5 shrink-0 items-center justify-center rounded border transition-colors",
                          isSelected
                            ? "border-sky-500 bg-sky-500"
                            : "border-slate-300 dark:border-slate-500",
                        )}
                      >
                        {isSelected && (
                          <svg
                            className="h-3 w-3 text-white"
                            fill="currentColor"
                            viewBox="0 0 20 20"
                          >
                            <path
                              fillRule="evenodd"
                              d="M16.707 5.293a1 1 0 010 1.414l-8 8a1 1 0 01-1.414 0l-4-4a1 1 0 011.414-1.414L8 12.586l7.293-7.293a1 1 0 011.414 0z"
                              clipRule="evenodd"
                            />
                          </svg>
                        )}
                      </div>
                    )}

                    {/*  */}
                    <div className="flex h-10 w-10 shrink-0 items-center justify-center rounded-full bg-gradient-to-br from-sky-400 to-sky-600 text-sm font-semibold text-white shadow-[0_10px_16px_-10px_rgba(2,132,199,0.9)]">
                      {(user.nickname || user.username).charAt(0).toUpperCase()}
                    </div>

                    {/*  */}
                    <div className="flex-1 min-w-0">
                      <p className="truncate text-sm font-medium text-slate-900 dark:text-white">
                        {user.nickname || user.username}
                      </p>
                      <p className="truncate text-xs text-slate-500 dark:text-slate-400">
                        @{user.username}
                      </p>
                    </div>

                    {/*  */}
                    {!isGroupMode && (
                      <svg
                        className="h-5 w-5 text-slate-400"
                        fill="none"
                        stroke="currentColor"
                        viewBox="0 0 24 24"
                      >
                        <path
                          strokeLinecap="round"
                          strokeLinejoin="round"
                          strokeWidth={2}
                          d="M9 5l7 7-7 7"
                        />
                      </svg>
                    )}

                    {/*  */}
                    {isGroupMode && isCreating && isSelected && (
                      <svg
                        className="h-5 w-5 animate-spin text-sky-500"
                        fill="none"
                        viewBox="0 0 24 24"
                      >
                        <circle
                          className="opacity-25"
                          cx="12"
                          cy="12"
                          r="10"
                          stroke="currentColor"
                          strokeWidth="4"
                        />
                        <path
                          className="opacity-75"
                          fill="currentColor"
                          d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"
                        />
                      </svg>
                    )}
                  </div>
                );
              })}
            </div>
          )}
        </div>

        {/*  */}
        {isGroupMode && (
          <div className="border-t border-white/40 p-4 dark:border-slate-200/10">
            <input
              type="text"
              value={groupName}
              onChange={(e) => setGroupName(e.target.value)}
              placeholder=""
              disabled={isCreating}
              className={cn(
                "lg-input w-full px-4 py-2 text-sm",
                "disabled:opacity-50",
              )}
            />
          </div>
        )}

        {/*  */}
        {error && (
          <div className="px-4 pb-2">
            <p className="text-sm text-red-500 dark:text-red-400">{error}</p>
          </div>
        )}

        {/*  */}
        <div className="flex border-t border-white/40 p-4 dark:border-slate-200/10">
          <button
            onClick={handleClose}
            disabled={isCreating}
            className={cn(
              "lg-btn-secondary rounded-xl px-4 py-2 text-sm",
              "disabled:opacity-50",
            )}
          >
            
          </button>
          {isGroupMode && (
            <div className="ml-auto flex items-center gap-2">
              <span className="text-xs text-slate-500 dark:text-slate-400">
                 {selectedUsers.length} 
              </span>
              <button
                onClick={handleCreateGroup}
                disabled={isCreating || selectedUsers.length < 2}
                className={cn(
                  "lg-btn-primary rounded-xl px-4 py-2 text-sm",
                  "disabled:opacity-50",
                )}
              >
                
              </button>
            </div>
          )}
        </div>
      </div>
    </div>
  );
}
